@charset "UTF-8";
body {
  background: #f9fafb;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 1216px;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: content-box;
}

.select__input {
  position: absolute;
  inset: 0;
  opacity: 0;
}

.btn-light {
  padding: 12px 20px;
  border: none;
  width: max-content;
  border-radius: 4px;
  background: #376df6;
  font: 600 16px/1.42857 "Roboto", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.btn-light:hover {
  background: #2b52b4;
}

.btn__blue {
  padding: 12px 20px;
  border: none;
  width: max-content;
  border-radius: 4px;
  background: #376df6;
  font: 600 16px/1.42857 "Roboto", sans-serif;
  color: #fff;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.btn__blue:hover {
  background: #2b52b4;
}
.btn__gray {
  padding: 12px 20px;
  border: none;
  width: max-content;
  border-radius: 4px;
  background: #f2f4f7;
  font: 600 16px/1.42857 "Roboto", sans-serif;
  color: #344054;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.btn__gray:hover {
  background: #c3c4c5;
}
.btn__wht {
  padding: 8px 14px;
  border: none;
  width: max-content;
  border-radius: 4px;
  border: 1px solid #eaecf0;
  background: #fff;
  font: 600 14px/1.42857 "Roboto", sans-serif;
  color: #344054;
  transition: all 0.3s;
}
.btn__wht:hover {
  background: #eaecf0;
}

.nav {
  background: #fff;
  z-index: 11;
  position: relative;
}
.nav__humburger, .nav__mob {
  display: none;
}
.nav__row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  padding: 20px 0;
}
.nav__row::before {
  content: "";
  display: block;
  width: 100vw;
  position: absolute;
  height: 1px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: #eaecf0;
}
.nav__logo {
  display: block;
  width: 100%;
  max-width: 128px;
}
.nav__logo img {
  width: 100%;
}
.nav__place {
  background: #f2f4f7;
  border-radius: 4px;
  padding: 8px 14px;
  display: grid;
  grid-template-columns: auto 20px;
  gap: 8px;
  margin: 0 auto 0 16px;
  cursor: pointer;
  position: relative;
  cursor: pointer;
  align-items: center;
}
.nav__place select {
  position: absolute;
  inset: 0;
  opacity: 0;
}
.nav__place p {
  font: 600 14px/1 "Roboto", sans-serif;
  color: #475467;
  transition: all 0.3s;
}
.nav__place svg {
  width: 20px;
}
.nav__place:hover p {
  color: #376df6;
}
.nav__place:hover svg path {
  stroke: #376df6;
}
.nav__lang {
  position: relative;
  cursor: pointer;
  display: grid;
  grid-template-columns: 20px auto 20px;
  gap: 4px;
  margin: 0 12px;
  padding: 8px 12px;
  background: #f2f4f7;
  border-radius: 4px;
}
.nav__lang select {
  position: absolute;
  inset: 0;
  opacity: 0;
}
.nav__lang p {
  font: 600 14px/1.42857 "Roboto", sans-serif;
  color: #98a2b3;
  transition: all 0.3s;
}
.nav__lang svg,
.nav__lang img {
  transition: all 0.3s;
}
.nav__lang svg path,
.nav__lang img path {
  transition: all 0.3s;
}
.nav__lang svg:last-child,
.nav__lang img:last-child {
  transform: rotate(90deg);
}
.nav__lang svg:last-child path,
.nav__lang img:last-child path {
  stroke: #98a2b3;
}
.nav__lang ul {
  position: absolute;
  width: 180px;
  top: calc(100% + 4px);
  right: 0;
  left: auto;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  border: 1px solid #eaecf0;
  border-radius: 8px;
  padding: 6px;
  z-index: 2;
  display: grid;
  grid-template-columns: 100%;
  gap: 4px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.nav__lang ul a {
  padding: 10px 38px 10px 8px;
  position: relative;
  transition: all 0.3s;
  font: 500 16px/1.5 "Roboto", sans-serif;
  color: #101828;
  display: block;
  border-radius: 6px;
}
.nav__lang ul a::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  opacity: 0;
  transition: all 0.3s;
  background: url("../img/blue_check.svg") center no-repeat;
  background-size: contain;
}
.nav__lang ul a.active {
  background: #f9fafb;
}
.nav__lang ul a.active::before {
  opacity: 1;
}
.nav__lang ul a:hover {
  background: #f9fafb;
}
.nav__lang:hover svg path {
  stroke: #376df6;
}
.nav__lang:hover p {
  color: #376df6;
}
.nav__lang_open ul {
  opacity: 1;
  pointer-events: auto;
}
.nav__lang_open svg:last-child,
.nav__lang_open img:last-child {
  transform: rotate(-90deg);
}
.nav .btn__blue {
  margin: 0 12px;
  font-size: 14px;
  padding: 8px 14px;
}
.nav__enter {
  font: 500 14px/1.42857 "Roboto", sans-serif;
  color: #2963f5;
  transition: all 0.3s;
  border-radius: 4px;
  padding: 8px 14px;
  background: #e3ebfe;
}
.nav__enter:hover {
  background: #c1d3ff;
}
.nav__fav {
  border-radius: 4px;
  padding: 8px;
  background: #e3ebfe;
  display: block;
  margin: 0 12px 0 0;
  transition: all 0.3s;
}
.nav__fav svg {
  display: block;
  width: 20px;
  height: 20px;
}
.nav__fav:hover {
  background: #c1d3ff;
}
.nav__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  position: relative;
}
.nav__list::before {
  content: "";
  display: block;
  width: 100vw;
  height: 1px;
  background: #eaecf0;
  position: absolute;
  bottom: 0;
  left: 100vw;
  left: 50%;
  transform: translateX(-50%);
}
.nav__list .btn__blue {
  margin: 0 0 0 auto;
}
.nav__list_row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  position: relative;
}
.nav__list_row ul {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__list_row a,
.nav__list_row p {
  display: grid;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #475467;
  padding: 16px 12px;
  position: relative;
}
.nav__list_row a::before,
.nav__list_row p::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #225ef5;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: all 0.3s;
  opacity: 0;
}
.nav__list_row a:hover, .nav__list_row a.active,
.nav__list_row p:hover,
.nav__list_row p.active {
  color: #225ef5;
}
.nav__list_row a:hover::before, .nav__list_row a.active::before,
.nav__list_row p:hover::before,
.nav__list_row p.active::before {
  opacity: 1;
}
.nav__value {
  border-radius: 4px;
  background: #f2f4f7;
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-columns: 60px 60px;
}
.nav__value div {
  display: block;
  padding: 3px 7px;
  font: 500 14px/1.5 "Roboto", sans-serif;
  color: #98a2b3;
  width: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  text-align: center;
}
.nav__value div.active {
  border: 1px solid #5482f7;
  background: #e3ebfe;
  color: #2963f5;
}
.nav__pers {
  position: relative;
  border: 1px solid #d0d5dd;
  border-radius: 4px;
  padding: 4px 12px;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #fff;
  display: grid;
  grid-template-columns: 28px 1fr 20px;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}
.nav__pers_arrow {
  transition: all 0.3s;
  transform: rotate(180deg);
}
.nav__pers img {
  width: 28px;
  height: 28px;
  border-radius: 100%;
  object: cover;
}
.nav__pers ul {
  position: absolute;
  top: calc(100% + 5px);
  right: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  border: 1px solid #eaecf0;
  border-radius: 8px;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  padding: 4px;
  width: 210px;
  display: grid;
  grid-template-columns: 100%;
  gap: 5px;
}
.nav__pers ul a {
  padding: 8px 12px;
  border-radius: 6px;
  padding: 8px 12px;
  display: grid;
  grid-template-columns: 24px 1fr;
  gap: 12px;
  align-items: center;
  transition: all 0.3s;
  position: relative;
}
.nav__pers ul a:hover {
  background: #f9fafb;
}
.nav__pers ul p {
  font: 500 16px/1.5 "Roboto", sans-serif;
  color: #475467;
}
.nav__pers ul li:last-child a::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #eaecf0;
  top: -3px;
  left: 0;
  position: absolute;
}
.nav__pers_name p {
  font: 600 12px/1.5 "Roboto", sans-serif;
  color: #344054;
}
.nav__pers_name p + p {
  font: 400 10px/1.3 "Roboto", sans-serif;
  color: #667085;
}
.nav__pers.active ul {
  opacity: 1;
  pointer-events: auto;
}
.nav__pers.active .nav__pers_arrow {
  transform: rotate(0);
}
.nav__drop {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 100vw;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  z-index: 50;
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
  transition: all 0.3s;
}
.nav__drop.active {
  opacity: 1;
  pointer-events: all;
}
.nav__drop .container {
  display: grid;
  grid-template-columns: 1fr 290px;
  gap: 20px;
}
.nav__drop_list {
  padding: 32px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
}
.nav__drop_list p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.55556;
  color: #1d2939;
  margin: 0 0 22px;
}
.nav__drop_list ul {
  display: grid;
  grid-template-columns: 1fr;
  gap: 28px;
  padding: 0 0 0 18px;
}
.nav__drop_list ul a {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #475467;
  transition: all 0.3s;
}
.nav__drop_list ul a:hover {
  color: #225ef5;
}
.nav__drop_list ul span {
  font-weight: 300;
  font-size: 12px;
  line-height: 1.3;
  color: #98a2b3;
}
.nav__drop_banner {
  position: relative;
  padding: 32px;
  background: #f9fafb;
  z-index: 2;
}
.nav__drop_banner::before {
  content: "";
  display: block;
  position: absolute;
  height: 100%;
  width: 1000vw;
  left: 0;
  z-index: -1;
  pointer-events: none;
  top: 0;
  background: #f9fafb;
}
.nav__drop_banner img {
  width: 100%;
  border-radius: 6px;
  display: block;
}
.nav__drop_banner p {
  margin: 24px 0 0;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #101828;
}
.nav__drop_banner p + p {
  margin: 4px 0 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #475467;
}
.nav__drop_banner a {
  margin: 12px 0 0;
  display: block;
  width: max-content;
  font-weight: 400;
}

fieldset:last-child h4 {
  border: none;
}
fieldset .mse2_number_inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  min-width: 300px;
}
fieldset .mse2_number_inputs label {
  font: 500 14px/1.42857 "Roboto", sans-serif;
  color: #344054;
}
fieldset .mse2_number_inputs label input {
  position: relative;
  height: auto;
  box-sizing: border-box;
  margin: 6px 0 0;
  padding: 8px 12px;
  font-size: 16px;
  color: #344054;
  opacity: 1;
  border: 1px solid #d0d5dd;
  border-radius: 4px;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #fff;
}

.itc-select,
fieldset {
  position: relative;
}
.itc-select__toggle,
.itc-select h4,
fieldset__toggle,
fieldset h4 {
  height: 100%;
  border: none;
  border-right: 1px solid #d0d5dd;
  border-radius: 0;
  padding: 10px 0;
  justify-content: center;
  gap: 8px;
  font: 600 14px/1.42857 "Roboto", sans-serif;
  color: #344054;
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.itc-select__toggle::after,
.itc-select h4::after,
fieldset__toggle::after,
fieldset h4::after {
  content: none;
}
.itc-select__toggle::before,
.itc-select h4::before,
fieldset__toggle::before,
fieldset h4::before {
  content: "";
  display: block;
  position: relative;
  order: 2;
  width: 20px;
  height: 20px;
  background: url("../img/arrow-blc-botton.svg") center no-repeat;
  background-size: contain;
  transition: all 0.3s;
}
.itc-select_show .itc-select__toggle::before,
fieldset_show .itc-select__toggle::before {
  transform: rotate(-180deg);
}
.itc-select .filter__fieldset,
fieldset .filter__fieldset {
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.itc-select__dropdown,
.itc-select .filter__fieldset,
fieldset__dropdown,
fieldset .filter__fieldset {
  top: calc(100% + 8px);
  right: 0;
  left: auto;
  width: max-content;
  border: 1px solid #eaecf0;
  border-radius: 8px;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  min-width: calc(100% - 20px);
  z-index: 4;
  position: absolute;
}
.itc-select__options,
.itc-select .filter__fieldset,
fieldset__options,
fieldset .filter__fieldset {
  display: grid;
  grid-template-columns: 100%;
  gap: 4px;
  padding: 6px;
}
.itc-select__options br,
.itc-select .filter__fieldset br,
fieldset__options br,
fieldset .filter__fieldset br {
  display: none;
}
.itc-select__option,
.itc-select .filter__fieldset span,
fieldset__option,
fieldset .filter__fieldset span {
  padding: 0;
  border-radius: 0;
  background: transparent !important;
}
.itc-select__option::before, .itc-select__option::after,
.itc-select .filter__fieldset span::before,
.itc-select .filter__fieldset span::after,
fieldset__option::before,
fieldset__option::after,
fieldset .filter__fieldset span::before,
fieldset .filter__fieldset span::after {
  content: none;
}
.itc-select__option a,
.itc-select .filter__fieldset span a,
fieldset__option a,
fieldset .filter__fieldset span a {
  color: #101828;
  border-radius: 6px;
  padding: 10px 10px 10px 8px;
  font: 500 16px/1.5 "Roboto", sans-serif;
  color: #101828;
  padding: 10px;
  gap: 8px;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  width: 100%;
}
.itc-select__option:hover a,
.itc-select .filter__fieldset span:hover a,
fieldset__option:hover a,
fieldset .filter__fieldset span:hover a {
  background: #f9fafb;
}
.itc-select__option_selected a,
.itc-select .filter__fieldset span_selected a,
fieldset__option_selected a,
fieldset .filter__fieldset span_selected a {
  background: #f9fafb;
}
.itc-select__option_selected a::after,
.itc-select .filter__fieldset span_selected a::after,
fieldset__option_selected a::after,
fieldset .filter__fieldset span_selected a::after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: url("../img/blue_check.svg") center no-repeat;
  background-size: contain;
}
.itc-select .filter__fieldset label,
fieldset .filter__fieldset label {
  position: relative;
}
.itc-select .filter__fieldset span,
fieldset .filter__fieldset span {
  cursor: pointer;
  color: #101828;
  border-radius: 6px;
  padding: 10px 10px 10px 8px;
  font: 500 16px/1.5 "Roboto", sans-serif;
  color: #101828;
  padding: 10px;
  gap: 8px;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 8px;
  width: 100%;
  position: relative;
}
.itc-select .filter__fieldset span:hover,
fieldset .filter__fieldset span:hover {
  background: #f9fafb !important;
}
.itc-select .filter__fieldset span::before,
fieldset .filter__fieldset span::before {
  border-radius: 4px;
  border: 1px solid #d0d5dd;
  opacity: 1;
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: transparent;
  background-size: contain;
  transition: all 0.3s;
}
.itc-select .filter__fieldset input,
fieldset .filter__fieldset input {
  cursor: pointer;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}
.itc-select .filter__fieldset input:checked + span::before,
fieldset .filter__fieldset input:checked + span::before {
  background: url("../img/blue_check.svg") center no-repeat;
}
.itc-select .filter__fieldset input:disabled + span,
fieldset .filter__fieldset input:disabled + span {
  opacity: 0.5;
}
.itc-select.open .filter__fieldset,
fieldset.open .filter__fieldset {
  opacity: 1;
  pointer-events: all;
}
.itc-select .mse2_number_slider span.ui-slider-handle,
fieldset .mse2_number_slider span.ui-slider-handle {
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background: #376df6 !important;
  transition: all 0s;
  padding: 0;
  border: none;
  top: calc(50% - 4px);
  margin: 0;
  transform: translateX(-50%);
}
.itc-select .mse2_number_slider span.ui-slider-handle::before,
fieldset .mse2_number_slider span.ui-slider-handle::before {
  content: none;
}
.itc-select .mse2_number_slider .ui-slider-range,
fieldset .mse2_number_slider .ui-slider-range {
  background: #376df6;
  opacity: 0.5;
}

.header {
  background: #f2f4f7;
  padding: 40px 0;
  position: relative;
}
.header .container {
  position: relative;
}
.header__swiper {
  position: relative;
}
.header__slide img {
  width: 100%;
  border-radius: 16px;
}
.header .swiper-pagination {
  width: calc(100% - 72px);
  left: 36px;
  bottom: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
}
.header .swiper-pagination span {
  border-radius: 4px;
  width: 60px;
  height: 2px;
  background: #98a2b3;
  display: block;
  margin: 0;
  opacity: 1;
  transition: all 0.3s;
}
.header .swiper-pagination span.swiper-pagination-bullet-active {
  background: #fff;
}

.public {
  padding: 40px 0;
  background: #fff;
}
.public__wrapper {
  margin: 24px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 16px;
}
.public__title {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 32px;
}
.public__arrow {
  display: grid;
  grid-template-columns: 44px 44px;
  gap: 8px;
}
.public__arrow a {
  border: 1px solid #d0d5dd;
  border-radius: 4px;
  padding: 12px;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #fff;
  transition: all 0.3s;
}
.public__arrow a:hover {
  background: #f2f4f7;
}

.catalog {
  padding: 40px 0;
  z-index: 2;
  position: relative;
  background: #fff;
}
.catalog__wrapper {
  display: grid;
